@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:wght@300&display=swap);
html {
  /* height: 100%; */
}

body {
  margin: 0;
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* height: 100%; */

  background: #16202e; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #16202e,
    #203a43
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.image-link-form {
  margin-top: 1em;
  width: 360px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

input {
  color: white;
  width: 100%;
  border: none;
  border-bottom: 2px solid var(--accent);
  background-color: rgba(255, 255, 255, 0);
  text-align: center;
  padding-bottom: 2px;
}

.link-bg {
  width: 100%;
  padding: 3em 2em;
  background-color: #ffffff1a;
  box-shadow: 1px 2px 5px #0b111a;
}

.detect-btn {
  position: absolute;
  top: -20px;
  height: 40px;
}

.face-recognition {
  margin: 0 auto;
}

.img-extent {
  text-align: center;
  display: flex;
  align-items: center;
  width: 80%;
  background-color: #16202e1a;
  box-shadow: 1px 2px 15px #16202e;
  margin: 0 auto;
  box-sizing: border-box;
}

.img-extent p{
  margin: 3em;
  color: #96a0ac;
}

.bounding-box {
  position: absolute;
  box-shadow: 0 0 0 3px #65C1FF inset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
}

:root {
  --accent: #f5e500;
}

html, body, div, h1, h2, p {
  margin: 0;
  padding: 0;
}

.App {
  width: 80%;
  margin: 0 auto;
}

.app-wrapper {
  display: grid;
  grid-gap: 4em;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  align-items: center;
  margin: 2em auto;
}

body {
  font-family: "Fira Sans", "arial", sans-serif;
  font-weight: 300;
}

button {
  background-color: #f5e500;
  background-color: var(--accent);
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: -1;
  opacity: 0.9;
}

.Tilt {
  box-shadow: 1px 2px 10px #333;
}

.info {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.info p {
    padding: 2em 0 1em 0;
}



